<template>
  <v-card elevation="0" :disabled="loading" :loading="loading">
    <BlockItem>
      <template v-slot:icon>
        mdi-message
      </template>
      <template v-slot:title>
        صندوق پیام ها
      </template>
        <v-row class="px-3" >
          <v-col class="py-0" cols="12" >

            <v-data-table
                hide-default-footer
                :headers="headers" :loading="loading" :items="data" item-key="id" disable-sort
            >
            </v-data-table>

          </v-col>
        </v-row>
    </BlockItem>
    <template v-slot:progress>
      <GDLoading/>
    </template>
  </v-card>
</template>

<script>
import BlockItem from "@/components/Misc/BlockItem";
import GDLoading from "@/components/Dashboard/Misc/GDLoading";
import axios from "axios";

export default {
  name: "MessagesDash",
  components: {GDLoading, BlockItem},
  created() {
    this.fetchMessages();
  },
  data () {
    return {
      loading : false,
      data : [],
    }
  },
  computed : {
    headers() {
      return [
        {
          text: 'متن پیام',
          align: 'start',
          class: 'header-table-gray',
          sortable: false,
          value: 'comment',
        },
        {
          text: 'نام فرستنده', class: 'header-table-gray',
          align: 'center',
          value: 'name',
        },
        {
          text: 'ایمیل فرستنده', class: 'header-table-gray',
          align: 'end',
          value: 'email',
        }
      ]
    },
  },
  methods : {
    fetchMessages () {
      this.loading = true;
      axios.get(this.$store.state.host + '/website/api/v1/website/messages').then(response=>{
        this.data = response.data.data;
      }).catch(error=>{
        this.errorHandle(error.response)
      }).finally(()=>{
        this.loading = false;
      })
    }

  }
}
</script>

